#gutenify-settings-app {
	.gutenify-setting-tab-panel {
		.components-tab-panel__tabs {
			background: #f8fafb;
			box-shadow: 0 0 5px -1px #cdcdcd;
			margin-bottom: 30px;
			margin-top: 10px;
		}
		.components-tab-panel__tabs-item {
			border-top: 2px solid transparent;

		}
		.active-tab {
			background: #fff;
			border-top: 2px solid #037e03;
		}
	}

	.gutenify-setting-panel-notice-area {
		margin: 10px 0 20px 0;
	}

	.gutenify-setting-panel-toolbar-area {
		padding: 10px 20px;
	}

	.gutenify-text-warning {
		color: #dca606;
	}

	.gutenify-text-success {
		color: #4caf50;
	}

	.gutenify-text-danger {
		color: #e91e63;
	}

	.gutenify-editor-tab-ovel {
		padding: 0;
		width: 100%;

		.components-tab-panel__tabs {
			border: none;
			border: 1px solid #a4afb7;
			display: flex;
			justify-content: space-around;
			margin-bottom: 10px;
			margin-top: 0;
			padding: 0;
			.components-tab-panel__tabs-item {
				background: #fff;
				border-radius: 0;
				display: flex;
				height: auto;
				justify-content: center;
				margin: 0;
				padding: 0;
				padding: 5px;
				text-align: center;
				width: 100%;
			}
			.components-tab-panel__tabs-item:hover,
			.components-tab-panel__tabs-item:focus,
			.components-tab-panel__tabs-item:active,
			.components-tab-panel__tabs-item.active-tab {
				background-color: #a4afb7;
				border-top-color: transparent;
				box-shadow: none;
				color: #fff;
				&::before {
					display: none;
				}
			}
		}
	}
}


.gutenify-field-wrapper.gutenify-field-grid {
	display: grid;
	grid-template-columns: 1fr 2fr;
	margin-bottom: 70px;
	max-width: 600px;
}

// React select
input[id^="react-select"] {
	box-shadow:none!important;
}

.components-card.gutenify-template-kit-browser-pro-notice{
	background-color: #4caf4f;
	color: #fff;
	margin: 20px 0;
	box-shadow: none;
	border-radius: 5px;
	a{
		color: #fff;
	}
}

.gutenify-field-control button.components-flex.components-color-palette__custom-color {
    height: 42px;
}
